@charset 'utf-8';
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-pagination {
  line-height: normal;
  text-align: center;
  font-size: 0;

  & .tui-page-btn {
    display: inline-flex;
    position: relative;

    width: 40px;
    height: 40px;
    padding: 0;
    margin-left: 0;

    color: $blackColor;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.33;

    text-decoration: none;
    justify-content: center;
    align-items: center;
    vertical-align: middle;

    border-radius: 5px;
  }

  & .tui-page-btn:hover {
    background-color: $backgroundGrayColor;
  }

  & .tui-is-selected,
  & strong {
    color: $whiteColor;
    background: $accentColor;
    border-color: $accentColor;
  }

  & .tui-is-selected:hover {
    background-color: $accentColor;
  }

  & .tui-first,
  & .tui-last,
  & .tui-prev,
  & .tui-next,
  & .tui-prev-is-ellip,
  & .tui-next-is-ellip {
    overflow: hidden;
    width: 40px;
    height: 40px;
    padding: 0;
    font-size: 0;
    line-height: 40px;
    white-space: nowrap;
    font-weight: normal;
  }

  & .tui-ico-first,
  & .tui-ico-prev,
  & .tui-ico-next,
  & .tui-ico-last,
  & .tui-ico-ellip {
    background: url(data:image/png;base64,)
      no-repeat 0 0;
    display: inline-block;
    vertical-align: middle;
    font-size: 0px;
  }

  & .tui-prev-is-ellip,
  & .tui-next-is-ellip {
    padding: 0;
    line-height: 1;
  }

  & .tui-prev {
    margin-right: 0;
  }

  & .tui-next {
    margin-left: 0;
  }

  & .tui-ico-first {
    width: 11px;
    height: 10px;
    background-position: 0px 0px;
  }

  & .tui-ico-prev {
    width: 6px;
    height: 10px;
    background-position: -26px 0px;
  }

  & .tui-ico-next {
    width: 6px;
    height: 10px;
    background-position: -13px -24px;
  }

  & .tui-ico-last {
    width: 11px;
    height: 10px;
    background-position: 0px -12px;
  }

  & .tui-ico-ellip {
    width: 10px;
    height: 18px;
    margin-top: 16px;
    vertical-align: top;
    background-position: 0px -24px;
  }

  & .tui-is-disabled:hover {
    background: $backgroundGrayColor;
    cursor: default;
  }

  & .tui-is-disabled .tui-ico-prev {
    background-position: -26px -12px;
  }

  & .tui-is-disabled .tui-ico-first {
    background-position: -13px 0px;
  }

  & .tui-is-disabled .tui-ico-next {
    background-position: -21px -24px;
  }

  & .tui-is-disabled .tui-ico-last {
    background-position: -13px -12px;
  }
}
